body{
    box-sizing: border-box;
}*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    color: white !important;
}
:root{
  --bs-body-bg: rgb(0 0 0 / 20%) !important;;
}
body {
    margin: 0;
    padding: 0;
    /* background-color: #f4f4f4; */
}
.bg-image{
  background-image: url('../../assets/images/2.png');

}
.bg-theme{
  background-size: 100% 100%;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  /* transition: background .2s; */
}
.bg-image{
  background-image: url('../../assets/images/2.png');
}
h3{
  color: white;
}
.transparent{
  background-color: transparent !important;
}
.card-blur{
  background-color: rgb(0 0 0 / 20%) !important;
  box-shadow: 0 .3rem .8rem rgba(0, 0, 0, .12) !important;
  margin-bottom: 1.5rem !important;
  border:  0.1px solid #ededf04f;
  color: white !important;
}
.thumnail{
  background-color: rgb(0 0 0 / 20%) !important;
  box-shadow: 0 .3rem .8rem rgba(0, 0, 0, .12) !important;
  border: 0.1px solid #ededf04f;
  color: white !important;
}
.card-hover{
  cursor: pointer;
}
.card-hover:hover{
  background-color: rgb(255 255 255 / 15%);
    border-radius: 10px;
    -webkit-box-shadow: 0 .5rem 1rem 0 rgba(0, 0, 0, .2);
    box-shadow: 0 .25rem .5rem 0 rgba(0, 0, 0, .2);
    transition:  all cubic-bezier(0.19, 1, 0.22, 1) cubic-bezier(.07,.95,0,1);
}
.tabs-container {
    display: flex;
    flex-direction: column;
    overflow: auto;
    width: 100%;
}
.table > :not(caption) > * > * {
  color: white !important;
  /* border:  0.1px solid #ededf04f; */
}
.tab {
    padding: 10px;
    cursor: pointer;
    transition: background-color 0.3s;
    align-items: center;
  display: flex;
  /* font-weight: 500; */
  line-height: 1.8;
  -webkit-transition: all .5s;
  transition: all .5s;
  white-space: nowrap;
}
.tab:hover{
  background: rgb(255 255 255 / 15%);
}
.tab i{
    padding: 0 10px;
}
.tab-content {
    display: none;
    padding: 10px;
}

.btn-active{
  background-color: #0d6efd;
}
.table-container{
  height: 300px; /* Set your desired height */
  overflow-y: auto;
}
.table-container-max{
  max-height: 300px; /* Set your desired height */
  overflow-y: auto;
}
.table-body {
  /* display: block; */
}
.table-container::-webkit-scrollbar {
  width: 0.5em;
}
.table-container::-webkit-scrollbar-thumb {
  background-color: #888; /* Set the color of the scrollbar thumb */
}
.tab-content.active {
    display: block;
}
.activeBar{
  background: rgb(255 255 255 / 15%);
  /* color: #1e90ff; */
  /* border-radius: 50px 0 0 50px; */
}
.main_container{
    display: flex;
    width: 100%;
}
.search{
  background-color: rgb(0 0 0 / 15%);
  border: 1px solid rgb(255 255 255 / 15%);
  padding-right: 2.5rem;
}
.search_controle{
  background-color: rgb(0 0 0 / 15%);
  border: 1px solid rgb(255 255 255 / 15%);
  /* padding-right: 2.5rem; */
}
.table-light{
  --bs-table-bg: rgb(255 255 255 / 15%);
  color: #fff;
  border-color: rgb(255 255 255 / 15%);
}
.left_div{
    position: fixed;
    width: 20%;
    height: 100vh;
  /* background-color: dodgerblue; */
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* border: 1px solid rgb(151, 151, 151); */
}
.top-menu{
  height: 100%;
  overflow: scroll;
}
.bottom-menu{
  display: flex;
  justify-content: center;
  align-items: center;
}
.bottom-menu p {
  padding: 10px;
  margin-bottom: 0rem;
}
.bottom-menu a{
  text-decoration: none;

}
.right_div{
    width: 80%;
    margin-left: 20%;
    height: 100vh;
    overflow-x: hidden;
}
.logo{
    padding: 10px 10px;
}
.thumnail{
    width: 60px;
    height: auto;
  transition: 0.5s;

}
.card-hover:hover .thumnail{
  position: relative;
  width: 150px;
  transition: 0.5s;
}
.card-hover:hover .text-truncate{
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  transition: 0.5s;

}
.updateVideo{
    margin-bottom: 10px;
}
.wrapButton{
    position: fixed;
    top: 10px;
    right: 10px;
}
.updateInfoHeading{
    /* background-color: #1e90ff; */
  padding: 10px;
  color: white;
}
.form-control-lg{
    border-radius: 0px !important;
    font-size: 16px !important;
}
.form-control:focus {
    color: var(--bs-body-color);
    background-color: var(--bs-body-bg);
    border-color: #86b7fe;
    outline: 0;
    box-shadow: 0 0 0 .25rem rgba(0, 0, 0, 0);
  }

.display-none{
    display: none !important;
  }
  .mobileNav{
    display:none;
  }

  .updateInfoHeading{
    /* background-color: #1e90ff; */
  padding: 10px;
  color: white;
}
.updateInfoHeading p {
  /* background-color: #1e90ff; */
  padding: 10px;
  color: white;
}
.logo img{
    width: 20%;
    height: auto;
}
.table{
        table-layout: fixed !important;
    }   
.menuHideBar{
  position: absolute;
  /* position: ; */
  right: -12px;
  background-color: #1e90ff;
  padding: 10px;
  border-radius: 20%;
  top: 17px;
  border: 2px solid white;

}
.adminDashbord{
  /* position: relative;
  top: 20px; */
  width: 100%;
  min-height: 200px;
  /* background-color:#1e90ff; */
  display: flex;
  align-items:center;
}
.adminDashbord .row{
  --bs-gutter-x: 0rem;
  --bs-gutter-y: 0;
}
.modal {
  --bs-modal-width: 90% !important;
}
.spinner {
   width: 11.2px;
   height: 11.2px;
   border-radius: 11.2px;
   box-shadow: 28px 0px 0 0 rgba(71,75,255,0.2), 22.7px 16.5px 0 0 rgba(71,75,255,0.4), 8.68px 26.6px 0 0 rgba(71,75,255,0.6), -8.68px 26.6px 0 0 rgba(71,75,255,0.8), -22.7px 16.5px 0 0 #474bff;
   animation: spinner-b87k6z 1s infinite linear;
   margin: 0 auto;
}
.wrapButton{
  z-index: 9999;
}
.wrap{
  margin-bottom: 20px;
}
.mobileNav{
  padding:10px;
}
.form-control, .form-select{
  border-radius: .375rem !important;
    border: 1px solid rgb(255 255 255 / 15%);
}
.custom-placeholder::placeholder {
  color: rgb(255 255 255 / 15%); /* Change this to the desired color */
}
hr{
  margin: 1rem 0;
  color: inherit;
  border: 0;
    border-top-width: 0px;
    border-top-style: none;
    border-top-color: currentcolor;
  border-top: 1px solid;
  opacity: .25;
}
.btn {
    --bs-btn-border-radius : 2px !important;
}
.custom-list{
  height: 300px;
  overflow: auto;
}
.h-300{
  min-height: 300px;
  max-height: 300px;
  overflow: scroll ;
}

.bg-head{
  background-color: rgb(255 255 255 / 15%);
}
.clamp-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
