.page_loader{
  background-color: black;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: sticky;
  top: 0;
  bottom: 0;
  z-index: 999;
}
.spinner-grow{
  --bs-spinner-width: 10px;
  --bs-spinner-height: 10px;
}



